@import url(./pageDefault.css);
@import url(./Releases.css);
@import url(./Artists.css);
@import url(./home.css);
@font-face {
  font-family: BelleFair;
  src: url(../Assets/Fonts/Bellefair-Regular.ttf); }

@font-face {
  font-family: Ramona-Bold;
  src: url(../Assets/Fonts/Ramona-Bold.ttf); }

@font-face {
  font-family: Ramona-Light;
  src: url(../Assets/Fonts/Ramona-Light.ttf); }

@font-face {
  font-family: Sango;
  src: url(../Assets/Fonts/SangoUppercase-Static.otf); }

@font-face {
  font-family: Benz;
  src: url(../Assets/Fonts/Benzol_personal_use.otf); }

* {
  /* text-align: center; */
  color: white; }

#root {
  margin: 0;
  padding: 0;
  display: flex;
  width: 100%;
  height: 100%;
  min-height: 100vh;
  background-color: #242424;
  background-image: url(../Assets/Images/background.gif);
  background-blend-mode: difference; }

html {
  padding: 0;
  margin: 0; }

body {
  margin: 0; }

.navbar {
  width: 220px;
  padding: auto;
  margin: 0;
  grid-row-start: 1; }
  .navbar .outerList {
    margin-top: 40px;
    color: white;
    font-family: Sango;
    font-size: 20px;
    text-decoration: none; }
    .navbar .outerList .logo {
      font-size: 30px;
      text-decoration: none; }
    .navbar .outerList .innerList {
      margin-top: 20px;
      font-family: Benz;
      text-decoration: none;
      list-style-type: none;
      padding-left: 0; }
      .navbar .outerList .innerList a {
        text-decoration: none; }
